@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@media only screen and (max-width: 750px){.services__title{font-size:2em}}.services__description{text-align:center;margin:0.1em 0;line-height:1.4;font-size:1.125rem;color:#4b4b4b}

.services-carousel{margin:40px 0}.services-carousel__wrapper{padding:3.2rem}@media only screen and (max-width: 750px){.services-carousel__wrapper{padding:2rem}}.services-carousel__slider{padding:0 200px;position:relative;margin-top:40px}@media only screen and (max-width: 1320px){.services-carousel__slider{padding:0 150px}}@media only screen and (max-width: 940px){.services-carousel__slider{padding:0 90px}}@media only screen and (max-width: 750px){.services-carousel__slider{padding:0 40px;margin-top:20px}}.services-carousel__item{display:flex;flex-direction:column;align-items:center;align-content:flex-start}.services-carousel__item:focus{outline:0}.services-carousel__item-icon{width:180px;height:180px;border-radius:50%;margin:35px 0;background:#cdcdcd url(data:image/png;base64,) no-repeat center center}@media only screen and (max-width: 400px){.services-carousel__item-icon{width:140px;height:140px;border-radius:50%;margin:20px 0}}.services-carousel__item-desc{text-align:center;max-width:220px}.services-carousel__item-title{text-transform:uppercase;color:#222222;font-size:1.5rem;line-height:1.2;margin:1rem 0}.services-carousel__item-info{font-size:1rem;font-weight:100;color:#777777}.services-carousel-arrow{width:33px;height:106px;background:url(data:image/png;base64,) no-repeat;cursor:pointer;margin:72px 0;position:absolute}@media only screen and (max-width: 400px){.services-carousel-arrow{margin:32px 0}}.services-carousel-arrow_prev{background-position:left center;left:0}.services-carousel-arrow_next{background-position:right center;right:0}.owl-carousel{position:static}.owl-nav{position:absolute;top:0;left:0;width:100%}

.services-list{margin:40px 0}.services-list__wrapper{padding:3.2em}@media only screen and (max-width: 750px){.services-list__wrapper{padding:2rem}}.services-list__description{text-align:center}.services-list__items{display:flex;flex-wrap:wrap;justify-content:space-around;margin-top:100px;padding:0 80px}@media only screen and (max-width: 1320px){.services-list__items{padding:0;justify-content:space-between}}@media only screen and (max-width: 940px){.services-list__items{margin-top:50px}}@media only screen and (max-width: 700px){.services-list__items{flex-direction:column;align-items:center}}.services-list__item{display:flex;align-items:flex-start;max-width:calc(50% - 40px);margin:20px 10px}@media only screen and (max-width: 700px){.services-list__item{max-width:100%}}.services-list__item-icon{width:60px;height:60px;border-radius:50%;background:#cdcdcd;margin-right:35px;flex-shrink:0}@media only screen and (max-width: 400px){.services-list__item-icon{margin-right:15px;width:40px;height:40px}}.services-list__item-desc{max-width:310px}.services-list__item-title{text-transform:uppercase;color:#222222;font-size:1.5rem;line-height:1.2;margin:5px 0}

:root,html,body{margin:0;padding:0;height:100%;min-height:100%;font-size:16px}@media only screen and (max-width: 940px){:root,html,body{font-size:14px}}body{font-family:'Titillium Web', sans-serif;background:#ecedee;min-height:100%}.app{max-width:100%;overflow:hidden}.wrapper{width:1280px;margin:0 auto;padding:0;overflow:hidden;background:#fff;box-sizing:border-box}@media only screen and (max-width: 1320px){.wrapper{width:900px}}@media only screen and (max-width: 940px){.wrapper{width:auto;max-width:100%;margin:20px}}h2{font-size:2.5em;font-weight:bold;text-transform:uppercase;text-align:center;margin:0;line-height:1.6;color:#222}

